import React from "react";
import "./accueil.css";
import Carousel from "react-bootstrap/Carousel";
import Fade from "react-reveal/Fade";
import { Link } from "react-router-dom";

function Accueil() {
  return (
    <section>
      <div className="accueil-main-content">
        <Fade left>
          <Carousel interval={1000} className="carousel-main">
            <Carousel.Item>
              <a href="/sites-externe/mikapiscine/index.html">
                <img
                  className="d-block w-100"
                  src="/img/mika-3.png"
                  alt="First slide"
                />
              </a>
            </Carousel.Item>
            <Carousel.Item>
              <a href="/sites-externe/sophiephoto/index.html">
                <img
                  className="d-block w-100"
                  src="/img/sophie-3.png"
                  alt="second slide"
                />
              </a>
            </Carousel.Item>
            <Carousel.Item>
              <a href="/sites-externe/jashoppan/index.html">
                <img
                  className="d-block w-100"
                  src="/img/jashoppan-3.png"
                  alt="Third slide"
                />
              </a>
            </Carousel.Item>
          </Carousel>
        </Fade>
      </div>
      <p>t</p>
      <p>t</p>
      <p>t</p>
      <p>t</p>
      <p>t</p>
      <p>t</p>
      <p>t</p>
      <p>t</p>
      <p>t</p>
    </section>
  );
}

export default Accueil;
