import React, { useState } from "react";
import "./header.css";
import { Link } from "react-router-dom";
import { HashLink } from "react-router-hash-link";
import { CgNotes } from "react-icons/cg";
import { FiMail } from "react-icons/fi";
import { BsPeopleCircle } from "react-icons/bs";
import { HiOutlineHome } from "react-icons/hi";
import { FiGithub } from "react-icons/fi";
import { GiHamburgerMenu } from "react-icons/gi";
import Nav from "react-bootstrap/Nav";

function Header() {
  const [displayNav, setDisplayNav] = useState(false);

  function showMenuMobile() {
    if (displayNav) {
      return (
        <div className="menu-mobile-global">
          <Nav
            onClick={() => {
              setDisplayNav(false);
            }}
          >
            <ul class="nav-menu">
              <li>
                <Link to="/">
                  <HiOutlineHome />
                  Accueil
                </Link>
              </li>
              <li>
                <Link to="/mesprojets">
                  <FiGithub />
                  Mes Projets
                </Link>
              </li>
              <li>
                <Link to="/cv">
                  <CgNotes />
                  Cv
                </Link>
              </li>
              <li>
                <Link to="/apropos">
                  <BsPeopleCircle />A propos
                </Link>
              </li>
              <li>
                <HashLink to="/#footer">
                  <FiMail />
                  Contact
                </HashLink>
              </li>
            </ul>
          </Nav>
        </div>
      );
    }
  }

  return (
    <header>
      <GiHamburgerMenu
        className="menu-mobile"
        onClick={() => {
          setDisplayNav(!displayNav);
        }}
      />
      <img id="logo" src="/img/logo3.png" alt="logo" />
      <div class="container">
        <nav id="nav-menu-container">
          <ul class="nav-menu">
            <li>
              <Link to="/">
                <HiOutlineHome />
                Accueil
              </Link>
            </li>
            <li>
              <Link to="/mesprojets">
                <FiGithub />
                Mes Projets
              </Link>
            </li>
            <li>
              <Link to="/cv">
                <CgNotes />
                Cv
              </Link>
            </li>
            <li>
              <Link to="/apropos">
                <BsPeopleCircle />A propos
              </Link>
            </li>
            <li>
              <HashLink to="/#footer">
                <FiMail />
                Contact
              </HashLink>
            </li>
          </ul>
        </nav>
      </div>
      {showMenuMobile()}
    </header>
  );
}

export default Header;
